import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

import { useConfiguration } from '../../api/internal'
import { useActiveConfigurationName } from '../../services/ActiveConfiguration'
import { useHomePathFromConfig } from '../ConfigurationsSettings/homepage'
import EmptyConfigs from '../EmptyConfigs'

const Index = () => {
  const { data } = useConfiguration()
  const { configurationName } = useActiveConfigurationName()
  const { homepagePath } = useHomePathFromConfig()
  const indexPath = homepagePath(configurationName || data?.configs?.[0]?.name || '')
  const navigate = useNavigate()

  useEffect(() => {
    if (indexPath) {
      navigate(indexPath)
    }
  }, [indexPath, navigate])

  return <EmptyConfigs></EmptyConfigs>
}

export default Index
